.hero {
  border-radius: 10px;
  background-color: #ebf0fa;
}
.hero__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 102px 72px;
}
.hero__main {
  max-width: 31.5%;
}
.hero__title {
  margin-bottom: 18px;
}
.hero__text {
  margin-bottom: 36px;
}
.hero__img {
  width: 50%;
  height: 100%;
  border-radius: 0 10px 10px 0;
  flex-shrink: 0;
  position: absolute;
  right: 0;
  top: 0;
}
.hero__img img {
  border-radius: 0 10px 10px 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

@media (max-width: 1400px) {
  .hero__container {
    flex-direction: column;
    padding: 0;
  }
  .hero__img {
    width: 100%;
    height: 360px;
    border-radius: 0 0 10px 10px;
    position: static;
  }
  .hero__img img {
    border-radius: 0 0 10px 10px;
  }
  .hero__main {
    padding: 24px;
    max-width: 100%;
    width: 100%;
  }
  .hero__text {
    margin-bottom: 24px;
    max-width: 680px;
  }
}
@media (max-width: 767px) {
  .hero__title {
    margin-bottom: 14px;
  }
  .hero__text {
    margin-bottom: 18px;
  }
  .hero__img {
    height: 220px;
  }
  .hero__btn {
    width: 100%;
  }
}